<template>
  <div class="step-two">
    <div class="title">
      上传参赛方案格式要求：PDF / PPT
    </div>
    <div class="upload" @click="upload">
      <img src="@/common/images/upload.png" alt="">
    </div>
    <div class="desc" >上传方案</div>
  </div>
</template>
<script>
import Bus from '../../common/bus';
export default {
  mounted() {
    // 文件选择后的回调
    Bus.$on('fileAdded', () => {
        console.log('文件已选择')
    });

    // 文件上传成功的回调
    Bus.$on('fileSuccess', () => {
        console.log('文件上传成功')
    });
  },
  methods: {
    upload() {
      // 打开文件选择框
      console.log(123);
      Bus.$emit('openUploader', {
          id: '1111'  // 传入的参数
      })
    }
  },
  destroyed() {
    Bus.$off('fileAdded');
    Bus.$off('fileSuccess');
  },
}
</script>
